/* Main Page styling */
.page-container {
  display: flex;
  flex-direction: column;
  position: relative;
  min-width: 1280px;
  width: 100%;
  height: 850px;
  overflow: hidden;
}

.modal-btn-main {
  display: none;
}

.main {
  width: 608px;
  height: 343px;
  padding-left: 16px;
}

.main h1 {
  font-family: Verdana, sans-serif;
  font-size: 34px;
  font-weight: 700;
  width: 608px;
  margin-bottom: 68px;
}
  
form {
  display: flex;
  gap: 40px;
  position: absolute;
}
  
.column {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
  
label {
  font-size: 14px;
  padding: 0 10px;
  font-weight: 700;
  color: #9B9FAA;
  text-transform: capitalize;
}

input {
  margin-bottom: 25px;
}
  
input,
select {
  font-weight: 700;
  font-size: 14px;
  border: none;
  width: 240px;
  border-bottom: 1px solid #ccc;
}
  
button {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  width: 220px;
  height: 43px;
  position: relative;
  padding: 13px 10px;
  font-size: 13px;
  line-height: 17.01px;
  font-weight: 600;
  color: white;
  background-color: #FC842D;
  border: none;
  border-radius: 30px;
  cursor: pointer;
  transition: all 0.3s ease;
  top: 280px;
  left: -280px;
}
  
button:hover {
  transform: scale(1.05);
  background-color: #ff7a1c;
}
  
  /* TABLETĂ (768px - 1280px) */
@media (max-width: 1280px) {
  .main { 
    min-width: 608px;
    max-width: 704px;
    width: 100%;
  }
  
  .main h1 {
    margin-top: 120px;
  }

  .main-page .main form {
    margin-top: 50px;
  }

  .page-container {
    min-width: 768px;
    width: 100%;
    max-height: 1279px;
    height: 1024px;
    overflow: hidden;
  }
}

@media (max-width: 768px) {
  .modal-btn-main {
    display: block;
    top: 0;
  }

  .main-page .header__nav {
    right: 0;
  }

  .main-page .logo__image {
    margin-left: 0;
  }

  .main-page .header {
    min-width: 320px;
    width: 100%;
    margin-left: 0;
    overflow: visible;
  }

  .main-page .header__logo {
    padding-left: 0;}

  .main h1 {
    font-size: 18px;
    min-width: 280px;
    width: 100%;
    max-width: 480px;
    padding-right: 20px;
    margin: 32px auto 34px;
  }

  .main {
    min-width: 280px;
    max-width: 704px;
    width: 100%;
    margin: 0 auto;
  }

  .page-container {
    min-width: 280px;
    max-width: 704px;
    width: 100%;
    height: 718px;
    margin: 0;
    margin-right: -30px;
  }

  .form {
    margin-top: -100px;
  }
}
